.data-access-requirement-title {
  margin-top: 30px;
}

.requirement-container {
  display: flex;
}

.register-text-link {
  color: rgb(77, 85, 144);
}

.terms-of-use-content {
  margin-top: 20px;
}

.button-container {
  &.hide {
    visibility: hidden;
  }
}

.view-terms-button {
  color: rgb(77, 85, 144);
}
.view-terms {
  &.hide {
    visibility: hidden;
  }
}

.accept-button-container {
  margin-top: 30px;
  align-content: center;
  justify-content: center;
  text-align: center;
}

.accept-button {
  background-color: rgb(77, 85, 144);
  padding: 10px 70px 10px 70px;
  color: white;
}

.not-accept-button-container {
  margin-top: 15px;
  align-content: center;
  justify-content: center;
  text-align: center;
  color: rgb(77, 85, 144);
}

.self-sign-access-title {
  margin-top: 20px;
}

.self-sign-access-certified-success-text {
  &.hide {
    visibility: hidden;
  }
}

.self-sign-access-verified-success-text {
  &.hide {
    visibility: hidden;
  }
}
.get-access-requirement-button {
  margin-left: 10px;
  background-color: rgb(77, 85, 144);
  color: white;
}
.check-mark-container {
  &.orange {
    background-color: orange;
  }
  &.green {
    background-color: rgb(39, 167, 69);
  }
  color: white;
  border-radius: 50%;
  padding: 15px;
  height: 15px;
  width: 15px;
  display: flex;
  align-items: center;
  align-self: baseline;
  margin-top: 20px;
  margin-right: 15px;
  margin-left: 5px;
  text-align: center;
  justify-content: center;
}
